.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.25);
    animation-name: appear;
    animation-duration: 300ms;
  }
  
  .modal-dialog {
    width: 100%;
    width: 80vw;
    background: white;
    position: relative;
    margin: 0 3%;
    padding: 2% 3% 5% 3%;
    max-height: calc(100vh - 40px);
    text-align: left;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: slide-in;
    animation-duration: 0.5s;
  }
  
  .modal-header {
    display: flex;
    align-items: center;
    padding: 1rem;
  }
  .modal-header {
    border-bottom: 1px solid #dbdbdb;
    justify-content: space-between;
  }
  .modal-close {
    cursor: pointer;
    padding: 1rem;
    margin: -1rem -1rem -1rem auto;
  }
  .modal-body {
    padding: 2%;
    overflow: auto;
    display: flex;
    justify-content: space-around;
    font-family: 'Amatic SC', cursive;
    font-size: 4vmin;
  }
  .modal-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .modal-icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  a {
    text-align: center;
  }
  .modal-title {
    margin: 0;
    text-align: center;
    font-family: 'Amatic SC', cursive;
    font-size: 6vmin;
  }
  
  @keyframes appear {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  @keyframes slide-in {
    from {
      transform: translateY(-150px);
    }
    to {
      transform: translateY(0);
    }
  }