.countryImg {
    width: 3vw;
    height: 2vw;

}

.countryButton {
    display: flex;
    flex-direction: column;
}

.countries {
    font-family: 'Amatic SC', cursive;
    font-size: 4vmin;
    width: 50%;
}

.searchLine {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.searchLine input {
    width: 40%;
    outline: none;
    border: none;
    border-bottom: 1px solid black;
    padding: 0;
    margin: 1%;
    height: 5vw;
    font-size: 4vmin;
    font-family: 'Amatic SC', cursive;
}

.searchLine h2 {
    font-size: 4vmin;
}

.searchedCountry {
    height: 60vh;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}
.pagination {
    width: 100%;
    margin-top: 5%;
}

.text {
    text-align: center;
    margin: 0;
}
.page {
    width: 2.5vw;
    background-color: white;
    border: 1px solid black;
    border-radius: 20%;
    margin: 1%;
}