.sights {
    border-bottom: 1px solid black;
    font-family: 'Amatic SC', cursive;
    font-size: 3vmin;
    display: flex;
    width: 25%;
    align-items: center;
    margin: 1%;
}
.sights-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.city-title {
    font-family: 'East Sea Dokdo', cursive;
    font-size: 10vmin;
    text-align: center;
    width: 100%;
    margin: 0;
}
hr {
    color: black;
    width: 95%;
    margin-top: 2%;
}
.city {
    width: 25%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-right: 1px solid black;
    margin: 1%;;
    padding: 2%;
}
.all-cities {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.city-bc {
    background-image: url(../../../public/city.png);
    width: 100%;
    height: 10vw;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.city-name {
    text-align: center;
}
.icon {
    background-image: url(../../../public/city-icon.png);
    width: 5vw;
    height: 5vw;
    background-position: center;
    background-size: cover
}
button {
    width: 30%;
    margin: 0;
    background-color: transparent;
}
.search-wrap {
    width: 5vw;
    height: 5vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.search {
    background-image: url(./search.png);
    width: 2vw;
    height: 2vw;
    background-position: center;
    background-size: cover
}
.search:hover {
    width: 3vw;
    height: 3vw;
}
.all-city-sights {
    width: 100%;
}
.title-sights {
    width: 100%;
    text-align: center;
    font-family: 'East Sea Dokdo', cursive;
    font-size: 7vmin;
}
.spinner-wrap {
    width: 100%;
    display: flex;
    justify-content: center;
}